;EACA,sBAAA;EACA,iBAAA;EACA,gBAAA;CACA;AACA;EACA,kBAAA;CACA;AACA,UAAA;AACA;EACA,iBAAA;EACA,qBAAA;CACA","file":"cancelRecord.vue","sourcesContent":["<template lang=\"html\">\n  <div class=\"\">\n    <herderBar></herderBar>\n    <div class=\"search\">\n      <el-select placeholder=\"请选择\" v-model=\"selectData\">\n        <el-option label='全部' value=\"0\"></el-option>\n        <el-option label='服务经理' value=\"1\"></el-option>\n    \n      </el-select>\n      <el-input placeholder=\"请输入搜索的名称\" class=\"input\" v-model=\"name\"></el-input>\n      <el-button  type=\"primary\" class=\"search_btn\" @click=\"search\">搜索</el-button>\n    </div>\n    <div class=\"\">\n      <table class=\"table\">\n        <tr>\n          <th>姓名</th>\n          <th>电话</th>\n          <th>头像</th>\n          <th>地区</th>\n          <th>操作时间</th>\n        </tr>\n        <tr v-for=\"item in dataList\">\n          <td class=\"name\">{{item.name}}</td>\n          <td>{{item.phone}}</td>\n          <td>\n            <img class='Head_portrait' :src=\"item.head_pic\">\n          </td>\n          <td>\n           <a href=\"javascript:;\" class=\"col-1EA\" @click=\"regionDialogVisible=true,region(item)\">查看</a>\n          </td>\n          <td>\n            <span>{{item.audit_time}}</span>\n          </td>\n        </tr>\n      </table>\n      <!--投诉人弹框-->\n      <el-dialog title=\"投诉人\" class=\"teamDialog\" width=\"500px\" :visible.sync=\"ComplaintVisible\" center>\n        <div>\n          <table class=\"table ComplaintTable\">\n            <tr>\n              <td>名称:</td>\n              <td>{{Complaintlist.name}}</td>\n            </tr>\n            <tr>\n              <td>类型:</td>\n              <td >\n                <div v-show=\"Complaintlist.type==1\">运营商</div>\n                <div v-show=\"Complaintlist.type==2\">市级代理</div>\n                <div v-show=\"Complaintlist.type==3\">维修厂</div>\n              </td>\n            </tr>\n            <tr>\n              <td>负责人:</td>\n              <td></td>\n            </tr>\n            <tr>\n              <td>电话:</td>\n              <td>{{Complaintlist.phone}}</td>\n            </tr>\n            <tr>\n              <td>地址:</td>\n              <td>{{Complaintlist.address}}</td>\n            </tr>\n          </table>\n        </div>\n      </el-dialog>\n      <!--投诉内容弹框-->\n      <el-dialog title=\"投诉内容\" class=\"teamDialog\" width=\"500px\" :visible.sync=\"ComplaintContentVisible\" center>\n        <div class=\"ComplaintContent\">\n          {{Complaintdata}}\n        </div>\n      </el-dialog>\n      <el-dialog title=\"地区\" :visible.sync=\"regionDialogVisible\" width=\"400px\" center >\n            <div >\n                <div>\n                    <strong style='font-size:16px'>省: </strong>\n                    <span v-for='item in reasonData'>\n                        {{item.name}}\n                        <div>\n                            <strong style='font-size:16px'>市: </strong>\n                            <span v-for='it in item.son'>\n                                {{it.name}}\n                                <div>\n                                    <strong style='font-size:16px'>区/县: </strong>\n                                    <span v-for='i in it.son'>{{i.name}}、</span>\n                                </div>\n                            </span>\n                        </div>\n                      \n                    </span> \n                </div>\n                \n            </div>\n           \n        </el-dialog>\n      <!--分页组件-->\n      <div class=\"page_center\">\n        <paging :page-count=\"pageCount\"   :page=\"page\" @index=\"paging\"></paging>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      token: window.sessionStorage.getItem(\"bbytoken\"),\n      pageCount: 1,  //总页数,默认1\n      page: 1,       //当前页\n      name: '',//当前搜索名字\n      selectData: \"0\",\n      dataList: [],\n      ComplaintVisible: false,//投诉弹框\n      ComplaintContentVisible: false, //投诉内容弹框\n      Complaintlist: {},//投诉人数据\n      Complaintdata: '', //投诉内容\n      regionDialogVisible: false,\n      reasonData: []\n    }\n  },\n  watch: {\n    name (newVal) {\n      if (!newVal) {\n        this.init()\n      }\n    },\n    selectData (newVal) { //监听select发生改变时,获取当前值,通过当前值,进去查询\n\n      var data = {\n        status: newVal\n      }\n      this.init(data)\n    }\n  },\n  methods: {\n    //初始化数据\n    init (data) {\n      var Data = {\n        token: this.token,\n        page: this.page,\n        search: data ? data.name : '',\n        ms_type: data ? data.ms_type : '',\n        status: data ? data.status : ''\n      }\n      this.$axios.post('Smoc/getAlready', Data).then(res => {\n        if (res.data.code == 1) {\n          this.dataList = res.data.data.list; //拿返回来的数据\n\n          this.pageCount = res.data.data.rows  //拿取总页数\n        } else {\n          this.dataList = {}\n          layer.msg(res.data.msg, { time: 1000 }) //如果code不等于一代表,没有数据，提示暂无数据\n        }\n      }).catch(err => console.log(err))\n    },\n    region (item) {\n      this.$axios.post('admin/Smoc/areaNum', {\n        token: this.token,\n        sm_id: item.uid,\n        id: item.id,\n        status: 1,\n        type: 2\n      }).then(res => {\n        if (res.data.code == 1) {\n          this.reasonData = res.data.data;\n        }\n      }).catch(err => console.log(err))\n    },\n    //搜索\n    search () {\n\n      var data = {\n        name: this.name,\n        ms_type: 2,\n        status: this.selectData\n      }\n      this.init(data)\n    },\n    //点击查看投诉人详情\n    ComplaintDialog (id) {\n      this.ComplaintVisible = true\n      this.$axios.post('Smoc/tsDetail', { token: this.token, id: id }).then(res => {\n        if (res.data.code == 1) {\n\n          this.Complaintlist = res.data.data\n        }\n      }).catch(err => console.log(err))\n    },\n    //投诉内容弹框\n\n    ComplaintContent (id) {\n      this.ComplaintContentVisible = true\n      this.$axios.post('Smoc/xcl', { token: this.token, id: id }).then(res => {\n        if (res.data.code == 1) {\n\n          this.Complaintdata = res.data.data\n        }\n      }).catch(err => console.log(err))\n    },\n    //操作分页组件\n    paging (e) {\n      //把当前页赋值给page\n      this.page = e;\n      // 当前页发生改变时调取注册审核列表接口,重新渲染\n      this.init()\n    }\n  },\n  mounted () {\n    this.init()\n    this.$store.commit('increment')\n  }\n}\n</script>\n\n<style lang=\"css\" scoped=\"scoped\">\n.Head_portrait {\n  width: 50px;\n  height: 50px;\n  vertical-align: middle;\n  margin: 5px 0;\n  border-radius: 50%;\n}\n.search {\n  margin: 15px 0 15px 40px;\n  display: flex;\n  align-items: center;\n}\n.search .input {\n  width: 25.9%;\n}\n.Conduct > a {\n  margin-left: 20px;\n}\n.name {\n  width: 10%;\n}\n.table th {\n  height: 50px !important;\n  line-height: 50px !important;\n}\n.content {\n  width: 150px;\n  margin: 0 auto;\n}\n.Reason {\n  width: 50px;\n  display: inline-block;\n  white-space: nowrap;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  text-align: left;\n  float: left;\n  margin-left: 40px;\n}\n.rel {\n  position: relative;\n}\n.See {\n  float: left;\n}\n/*投诉人弹框*/\n.ComplaintTable {\n  margin-bottom: 20px;\n}\n.ComplaintTable > tr > td {\n  width: 50% !important;\n  text-align: left;\n  padding: 0 10px;\n}\n.ComplaintTable > tr > td:last-child {\n  text-align: right;\n}\n/*投诉内容弹框*/\n.ComplaintContent {\n  margin-top: 20px;\n  padding-bottom: 40px;\n}\n</style>\n<style >\n.el-select .el-input {\n  width: 130px;\n  margin-right: 20px;\n}\n\n.search_btn {\n  border-radius: 0;\n  height: 38px;\n  width: 120px;\n}\n.input-with-select .el-input-group__prepend {\n  background-color: #fff;\n}\n.el-dialog__header {\n  padding-bottom: 20px;\n  border-bottom: 1px solid #e6e6eb;\n}\n.el-dialog__body {\n  padding-top: 0 !important;\n}\n</style>\n"],"sourceRoot":""}]);

